<template>
  <div class="auth-wrapper auth-v2">
    <b-row class="auth-inner m-0">
      <!-- Brand logo-->
      <b-link class="brand-logo">
        <!-- <vuexy-logo /> -->
        <b-img :src="require('@/assets/images/logo/header-logo.png')" />
        <h2 class="brand-text text-danger ml-1">
          ML OEx
        </h2>
      </b-link>
      <!-- /Brand logo-->
      <!-- Left Text-->
      <b-col
        lg="8"
        class="d-none d-lg-flex align-items-center p-5"
      >
        <div
          class="w-100 d-lg-flex align-items-center justify-content-center px-5"
        >
          <b-img
            fluid
            :src="imgUrl"
            alt="Login V2"
          />
        </div>
      </b-col>
      <!-- /Left Text-->
      <!-- Login-->
      <b-col
        lg="4"
        class="d-flex align-items-center auth-bg px-2 p-lg-5"
      >
        <b-col
          sm="8"
          md="6"
          lg="12"
          class="px-xl-2 mx-auto"
        >
          <b-card-title
            title-tag="h2"
            class="font-weight-bold mb-1"
          >
            Welcome to ML OEx - MLhuillier Online Exam! 👋
          </b-card-title>
          <b-card-text class="mb-2">
            Please sign-in to your account to start the exam
          </b-card-text>

          <!-- form -->
          <validation-observer ref="loginValidation">
            <b-form
              class="auth-login-form mt-2"
              @submit.prevent
            >
              <!-- email -->
              <b-form-group
                label="Email"
                label-for="login-email"
              >
                <validation-provider
                  #default="{ errors }"
                  name="Email"
                  rules="required|email"
                >
                  <b-form-input
                    id="login-email"
                    v-model="data.email"
                    :state="errors.length > 0 ? false : null"
                    name="login-email"
                    placeholder="name.test@mlhuillier.com"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>

              <!-- forgot password -->
              <b-form-group>
                <div class="d-flex justify-content-between">
                  <label for="login-password">Password</label>
                  <b-link
                    :to="{
                      name: 'reset-password',
                    }"
                  >
                    <small>Forgot Password?</small>
                  </b-link>
                </div>
                <validation-provider
                  #default="{ errors }"
                  name="Password"
                  rules="required"
                >
                  <b-input-group
                    class="input-group-merge"
                    :class="errors.length > 0 ? 'is-invalid' : null"
                  >
                    <b-form-input
                      id="login-password"
                      v-model="data.password"
                      :state="errors.length > 0 ? false : null"
                      class="form-control-merge"
                      :type="passwordFieldType"
                      name="login-password"
                      placeholder="············"
                    />
                    <b-input-group-append is-text>
                      <feather-icon
                        class="cursor-pointer"
                        :icon="passwordToggleIcon"
                        @click="togglePasswordVisibility"
                      />
                    </b-input-group-append>
                  </b-input-group>
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>

              <!-- submit buttons -->
              <b-button
                type="submit"
                variant="danger"
                block
                @click="validationForm"
              >
              <b-spinner small v-show="loader"></b-spinner>
                Sign in
              </b-button>
              <p
                v-if="isError"
                id="isError"
              >
                Incorrect Credentials
              </p>
            </b-form>
          </validation-observer>
          <b-card-text class="text-center mt-2">
            <span>Don't have an account yet? </span>
            <b-link :to="{ name: 'registration' }">
              <span>&nbsp;Register</span>
            </b-link>
          </b-card-text>
        </b-col>
      </b-col>
      <!-- /Login-->
    </b-row>
  </div>
</template>

<script>
/* eslint-disable global-require */
// eslint-disable-next-line
/* eslint-disable */
/* eslint-disable eol-last */
import { ValidationProvider, ValidationObserver } from 'vee-validate'
// import VuexyLogo from '@core/layouts/components/Logo.vue'
import {
  BSpinner,
  BRow,
  BCol,
  BLink,
  BFormGroup,
  BFormInput,
  BInputGroupAppend,
  BInputGroup,
  BCardText,
  BCardTitle,
  BImg,
  BForm,
  BButton,
} from 'bootstrap-vue';
import { required, email } from '@validations'
import { togglePasswordVisibility } from '@core/mixins/ui/forms'
import store from '@/store/index'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
// import { mapActions } from 'vuex'
// import * as userTypes from '../store/types/users'
// import * as auth from '../store/module/auth'


export default {
  components: {
    BSpinner,
    BRow,
    BCol,
    BLink,
    BFormGroup,
    BFormInput,
    BInputGroupAppend,
    BInputGroup,
    BCardText,
    BCardTitle,
    BImg,
    BForm,
    BButton,
    // VuexyLogo,
    ValidationProvider,
    ValidationObserver,
  },
  mixins: [togglePasswordVisibility],
  data() {
    return {
      loader: false,
      isError: false,
      status: '',
      data: {
        password: '',
        email: '',
      },
      sideImg: require('@/assets/images/pages/login-accept-task.svg'),
      // validation rulesimport store from '@/store/index'
      required,
      email,
    }
  },
  computed: {
    passwordToggleIcon() {
      return this.passwordFieldType === 'password' ? 'EyeIcon' : 'EyeOffIcon'
    },
    imgUrl() {
      if (store.state.appConfig.layout.skin === 'dark') {
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        this.sideImg = require('@/assets/images/pages/login-accept-task.svg')
        return this.sideImg
      }
      return this.sideImg
    },
  },
  mounted() {
    // console.clear()
  },
  methods: {
    // ...mapActions({
    //   postUser:userTypes.ACTION_SET_LOGIN
    // }),
    
    validationForm() {
    
      this.$refs.loginValidation.validate().then(async success => {
        if (success) {
          this.loader = true
          const login = await this.$store.dispatch('UserLogin', this.data)
          const token = this.$store.getters.StateToken
          sessionStorage.setItem('token', token)
          const message = login.data.message
          if (!token) {
            this.loader = false
            this.$toast({
            component: ToastificationContent,
            props: {
              // title: 'Email or Password is incorrect',
              title: `${message}`,
              icon: 'EditIcon',
              variant: 'danger',
            },
          }) 
          } else {
            let to = localStorage.getItem('take_exam')
            
            if (to) {
              to = JSON.parse(to)
              console.log(to)
              window.location.href = `${window.location.origin}/user/take-exam/${to.params.id}`
            }
            // window.location.href = `${window.location.origin}/user/take-exam`
            // this.$router.push({ name: 'home' })
          }
          // this.$router.push({ name: 'home' })
          // if (this.$store.getters['StateToken']) {
          //   setTimeout(() => {
          //     this.$router.push({ name: 'home' })
          //   },1500)
          //     this.$toast({
          //     component: ToastificationContent,
          //     props: {
          //       title: 'Form Submitted',
          //       icon: 'EditIcon',
          //       variant: 'success',
          //     },
          //   })     
          // }
        // }else {
        //   this.isError = true,
        //   this.$toast({
        //     component: ToastificationContent,
        //     props: {
        //       title: 'Email or Password is incorrect',
        //       icon: 'EditIcon',
        //       variant: 'danger',
        //     },
        //   }) 
        }
      })
    },
    }
}
</script>

<style lang="scss">
@import "@core/scss/vue/pages/page-auth.scss";
</style>



